import { base } from "../../../net/proto/proto";
import { BattleBaseComp } from "../BattleBaseComp";
import { battleLogicMgr } from "../BattleLogicMgr";
import { BattleTerrainGrid } from "../BattleTerrainGrid";
/**
 * 房间区域数据
 */
export class Out_BattleRoomOpOutput_TerrainData_2d extends BattleBaseComp<base.IBattleRoomOpOutput_TerrainData> {
    
    protected start(): void {
        if (!this.data) {
            console.error(`房间数据更新失败---->房间：${this.roomId}回合：${this.round}ownerPlayerId: ${this.ownerPlayerId} data:${this.data}`)
            this.exit();
            return;
        }
        let terrainGrildList: BattleTerrainGrid[] = battleLogicMgr.terrainGrildList;
        for (let terrainGrild of terrainGrildList) {
            if (terrainGrild.terrainData.instId == this.data.terrainData.instId) {
                terrainGrild.setData(this.data.terrainData);
            }
        }
        this.exit();
    }

    protected reset(): void {
       
    }
}